body {
	font-family: Verdana;
	margin: 0;
}

body * {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.app {
	height: 100vh;
	overflow-y: hidden;
	display: -ms-flexbox;
	display: flex;
}

.canvas {
	width: 100%;
	height: 100%;
	cursor: crosshair;
}

h1 {
	margin: 0;
}

/*
    NavBar
*/

.nav-bar {
	background: #59646e;
}

.nav-bar__tool {
	width: 40px;
	height: 40px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.nav-bar__tool > svg {
	width: 80%;
	height: 80%;
	fill: #fff;
	margin: 3px;
}

.nav-bar__tool--active {
	background: #b5226e;
}

/*
Tool Panels
*/

.tool-panel {
	background: #eff0f3;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	width: 222px;
}

.tool-panel div:first-child {
	padding: 0 10px;
}

.tool-panel section {
	margin: 20px 0;
}

.tool-panel label {
	font-weight: bold;
}

.tool-panel .button {
	background: #b5226e;
	padding: 10px;
	color: #fff;
	font-size: 14px;
	font-weight: bold;
	border-radius: 3px;
	border: none;
}

.current-selected-box {
	border: solid 2px #333;
}

/* Pen panel*/
.line-types {
	margin-top: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row;
	    flex-flow: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.line-types label {
	padding: 5px;
	font-weight: 300;
	margin-right: 2px;
}

.type-unselected {
	background-color: #ddd;
}

.type-selected {
	background-color: #5fa7fa;
	color: #fff;
}

.line-types input {
	display: none;
}

.panels-pen-colors {
	display: -ms-flexbox;
	display: flex;
	margin-top: 10px;
	height: 23px;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.panels-pen-colors div {
	width: 23px;
	margin-right: 4px;
	border-radius: 3px;
}

/* stamp */
.stamp {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	height: 100%;
}
.stamp-images {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	overflow-y: scroll;
	-ms-flex-align: start;
	    align-items: flex-start;
	-ms-flex: 2 1;
	    flex: 2 1;
	margin-bottom: 10px;
}

.stamp-image {
	max-width: 150px;
	max-height: 100px;
	margin-bottom: 10px;
}

.stamp-image-blur {
	-webkit-filter: opacity(50%);
	        filter: opacity(50%);
}

.stamp-file-upload {
	max-width: 180px;
}

/* save */
.save {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	height: 100%;
}

.download {
	text-decoration: none;
}

.save-snapshots {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: start;
	    align-items: flex-start;
	overflow-y: scroll;
	-ms-flex: 2 1;
	    flex: 2 1;
	margin-bottom: 10px;
}

.save-snapshots img {
	border-radius: 4px;
	max-width: 180px;
	max-height: 200px;
	margin-bottom: 5px;
}



